.empty-item {
  min-width: 150px;
  border: 1px solid black;
  border-radius: 20px;
  max-width: 20%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#a3bded), to(#6991c7));
  background-image: -webkit-linear-gradient(bottom, #a3bded 0%, #6991c7 100%);
  background-image: -o-linear-gradient(bottom, #a3bded 0%, #6991c7 100%);
  background-image: linear-gradient(to top, #a3bded 0%, #6991c7 100%);
}


