body {
  background: #C9CCD3;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.50)), to(rgba(0,0,0,0.50)));
  background-image: -webkit-linear-gradient(top, rgba(255,255,255,0.50) 0%, rgba(0,0,0,0.50) 100%);
  background-image: -o-linear-gradient(top, rgba(255,255,255,0.50) 0%, rgba(0,0,0,0.50) 100%);
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.50) 0%, rgba(0,0,0,0.50) 100%);
  background-blend-mode: lighten;
}

#Main h1 {
  text-align: center;
  color: darkviolet;
}



